import { Component, OnInit } from '@angular/core';
import { VendorContactService, VendorVendorService } from 'src/app/sdk/asset/services';
import { ServiceService } from '../../vendor-service/service.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Contact, Vendor } from 'src/app/sdk/asset/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private vendorContactService: VendorContactService,
        private vendorVendorService: VendorVendorService,
        private serviceService: ServiceService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,

    ) { }

    dataContact: Contact

    listVendor: Vendor[] = []

    id_vendor: string = ''
    id_contact: string = ''

    ngOnInit() {
        this.id_contact = this.activatedRoute.snapshot.paramMap.get('id_contact')
        this.serviceService.getIdVendor().subscribe(data => {
            this.id_vendor = data
            console.log('id pelamar paja tu ' + this.id_vendor)

            this.form()
            this.getData()
        })
    }

    getData() {
        this.vendorContactService.getVendorContactsId(this.id_contact).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Contact>
    form() {
        this.dataForm = this.fb.group<Contact>(
            {
                name_contact: new FormControl('', [Validators.required]),
                ref_module: new FormControl(''),
                ref_id_module: new FormControl(''),
                phone: new FormControl(''),
                email: new FormControl(''),
                country: new FormControl(''),
                address: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                logo: new FormControl(''),
                id_contact: new FormControl(''),
                id_user: new FormControl(''),
            }
        )
    }


    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.dataContact = this.dataForm.value;
        this.vendorContactService.putVendorContactsId({id: this.id_contact, body: this.dataContact}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/vendor/detail-vendor',this.id_vendor,'contact','list'])
    }

}
