import { Component, OnInit } from '@angular/core'
import { VendorContactService } from 'src/app/sdk/user/services'
import { VendorVendorService, VendorManufactureService } from 'src/app/sdk/asset/services'
import { Contact, Manufacture, Vendor } from 'src/app/sdk/asset/models'
import { NgxSpinnerService } from 'ngx-spinner'
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router } from '@angular/router'
import { Kalender_perusahaan } from 'src/app/sdk/hr/models'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
    constructor(
        private vendorContactService: VendorContactService,
        private vendorManufactureService: VendorManufactureService,
        private vendorVendorService: VendorVendorService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router

    ) { }

    dataContact: Contact

    listVendor: Vendor[] = []
    listManufacture: Manufacture[] = []



    ngOnInit() {
        this.form()

    }

    getPreeData() {

        if(this.dataForm.value.ref_module == 'Manufacture') {
            this.vendorManufactureService.getVendorManufactures().subscribe(data => {
                this.listManufacture = data.data
            })
        } else {
            this.vendorVendorService.getVendorVendors().subscribe(data => {
                this.listVendor = data.data
            })
        }
    }


    dataForm: FormGroupTypeSafe<Contact>
    form() {
        this.dataForm = this.fb.group<Contact>(
            {
                name_contact: new FormControl('', [Validators.required]),
                ref_module: new FormControl('',),
                ref_id_module: new FormControl('',),
                phone: new FormControl('',),
                email: new FormControl('',),
                country: new FormControl('',),
                address: new FormControl('',),
            }
        )
    }


    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.dataContact = this.dataForm.value;
        this.vendorContactService.postVendorContacts(this.dataContact).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/vendor/contact/list'])
    }
}
