import { Component, OnInit } from '@angular/core';
import { VendorContactService } from 'src/app/sdk/asset/services';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { Contact } from 'src/app/sdk/asset/models';
import { ServiceService } from '../../vendor-service/service.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private vendorContactService: VendorContactService,
        private serviceService: ServiceService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    id_vendor: string = ''
    ngOnInit() {
        this.serviceService.getIdVendor().subscribe(data => {
            this.id_vendor = data
            console.log('id pelamar paja tu ' + this.id_vendor)
        })
        this.getData()
    }

    listOfDisplayData: Contact[] = []
    listOfData: Contact[] = []
    getData() {
        this.spinner.show()
        this.vendorContactService.getVendorContacts(JSON.stringify({ref_id_module: this.id_vendor})).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()
            }
        )
    }

    reset(): void {
        this.searchValue = '';
        this.search();
    }

    sortName: string | null = 'created_at';
    sortValue: string | null = 'desc';
    sort(sort: { key: string; value: string }): void {
        this.sortName = sort.key;
        this.sortValue = sort.value;
        this.search();
    }
    searchValue: string = "";
    search(): void {
        const data = this.listOfData.filter(item => this.filterFuctionService.filterFunc(item, this.searchValue));
        /** sort data **/
        if (this.sortName && this.sortValue) {
            this.listOfDisplayData = data.sort((a, b) =>
                this.sortValue === 'ascend'
                    ? a[this.sortName!] > b[this.sortName!]
                        ? 1
                        : -1
                    : b[this.sortName!] > a[this.sortName!]
                        ? 1
                        : -1
            );
        } else {
            this.listOfDisplayData = data;
        }
    }

    delete(id) {
        this.spinner.show();
        this.vendorContactService.deleteVendorContactsId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }

}
